.page-intro {
  $self: &;
  &__intro-1 {
    margin-bottom: 14rem;
  }
  &__timeline {
    padding-left: 2rem;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    margin-bottom: 3rem;
    &--single {
      #{$self}__date {
        flex-basis: auto;
      }
    }
  }
  &__date {
    flex-basis: 15.5rem;
    &:nth-child(1) {
      margin-right: 4rem;
    }
    &:nth-child(2) {
      margin-right: 5.5rem;
    }
    &:nth-child(3) {
      margin-right: 5rem;
    }
    &:nth-child(4) {
      flex-basis: 10rem;
    }
    p {
      font-size: 3rem;
      color: #efc260;
      line-height: 1.2;
      text-align: center;
      &:nth-child(2) {
        color: #ffe09e;
        padding-top: 1rem;
      }
    }
  }
  &__timeline-img {
    width: 68.2rem;
    height: 14rem;
    margin-bottom: 1.6rem;
    margin-left: 2rem;
  }
  &__prize {
    background-color: #47484d;
    width: 618px;
    margin: 0 auto 14rem;
    padding: 5rem 0;
    position: relative;
    box-sizing: border-box;
    &--pattern-1 {
      background-image: url('../images/prize-pattern-1.jpg');
      background-position: center top;
      background-repeat: no-repeat;
    }
    &--pattern-2 {
      height: 69rem;
      background-image: url('../images/prize-pattern-2.jpg');
      background-position: center top;
      background-repeat: no-repeat;
      margin-bottom: 12rem;
    }
  }
  &__prize-text {
    font-size: 3rem;
    color: #efc260;
    text-align: center;
    line-height: 1.4;
    span {
      color: #ffe09e;
      padding-right: 1rem;
    }
  }
  &__prize-winner {
    width: 67.7rem;
    height: 59.3rem;
    position: absolute;
    top: 6rem;
    left: 50%;
    transform: translate3d(-50%,0,0);
  }
  &__champion {
    position: relative;
  }
  &__champion-statue {
    position: relative;
    height: 42.7rem;
  }
  &__champion-statue-img {
    width: 78.9em;
    height: 42.7rem;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate3d(-50%,0,0);
  }
  &__champion-statue-cht {
    width: 36.5em;
    height: 44.7rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(54%,-32%,0);
    z-index: 10;
  }
  &__champion-statue-info {
    text-align: center;
    h4 {
      font-size: 3.5rem;
      color: #efc260;
      margin-bottom: 2rem;
    }
    p {
      font-size: 3rem;
      color: #a6a6a6;
      line-height: 1.4;
    }
  }
}