.page-content {
  position: relative;
  &__wrap {
    padding-top: 6rem;
  }
  &__title {
    margin-bottom: 6rem;
    &.no-mb {
      margin-bottom: 1.6rem;
    }
    img {
      height: 5rem;
      display: block;
      margin: 0 auto;
    }
  }
  &__ul-list {
    padding: 0 2rem;
    text-align: center;
    li {
      text-align: left;
      padding-left: 4rem;
      position: relative;
      font-size: 3rem;
      color: #a6a6a6;
      line-height: 1.4;
      margin-bottom: 1rem;
      &::before {
        position: absolute;
        left: 0.8rem;
        top: 1rem;
        content: "";
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 1rem 2rem 1rem;
        border-color: transparent transparent #a6a6a6 transparent;
      }
      &.round {
        color: #efc260;
        display: inline-block;
        &::before {
          border-bottom-color: #efc260;
        }
      }
    }
  }
  &__sub-title {
    position: absolute;
    top: -2rem;
    left: 50%;
    transform: translate3d(-50%,0,0);
    img {
      height: 4.3rem;
      display: block;
      margin: 0 auto;
    }
    &--champion {
      position: relative;
      top: inherit;
      left: inherit;
      transform: translate3d(0,0,0);
    }
    &--round {
      position: relative;
      top: inherit;
      left: inherit;
      transform: translate3d(0,0,0);
    }
  }
}

.page {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  transition: opacity 500ms ease-in-out;
  pointer-events: none;
  &.is-active {
    pointer-events: auto;
    opacity: 1;
  }
}