.tab-nav {
  display: flex;
  width: 100%;
  &__btn {
    background-color: #212226;
    color: #a7b2b8;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    text-align: center;
    font-size: 3rem;
    line-height: 3.7rem;
    padding: 2.4rem 0;
    transition: all 300ms ease-in-out;
    border-bottom: 0.2rem solid #212226;
    font-weight: 300;
    &:hover {
      background-color: #333438;
      color: #fff;
    }
    @include susy-breakpoint($s-320, $c8) {
      &:hover {
        background-color: #212226;
        color: #a7b2b8;
      }
    }
    &.is-active {
      background-color: #333438;
      color: #fff;
    }
  }
}