.page-rule {
  &__intro-text {
    font-size: 3rem;
    line-height: 1.4;
    color: #efc260;
    text-align: center;
    margin-bottom: 4rem;
  }
  &__intro-1 {
    margin-bottom: 4rem;
  }
  &__intro-2 {
    margin-bottom: 4rem;
  }
  &__game-method {
    background: transparent url('../images/game-method@2x.png') no-repeat center center;
    background-size: cover;
    width: 74.5rem;
    height: 41.8rem;
    margin: 0 auto;
    display: flex;
    justify-content: center;
  }
  &__game-method-item {
    flex-basis: 50%;
    padding-top: 8rem;
    h3 {
      margin-bottom: 2.6rem;
      img {
        height: 3.6rem;
        display: block;
        margin: 0 auto;
      }
    }
    p {
      font-size: 3rem;
      color: #efc260;
      line-height: 1.333;
      text-align: center;
      text-shadow: 0 0.1rem 0.2rem rgba(0,0,0,0.9);
    }
    &:nth-child(2) {
      p {
        color: #88cbff;
      }
    }
  }
  &__round {
    text-align: center;
    margin-bottom: 3rem;
    h4 {
      font-size: 3rem;
      color: #ffe09e;
      line-height: 1.4;
      padding-top: 1rem;
    }
    p {
      font-size: 3rem;
      color: #efc260;
      line-height: 1.4;
    }
  }
  &__btn-detail {
    width: 49.1rem;
    height: 10.8rem;
    display: block;
    margin: 1rem auto 0;
    position: relative;
    font-size: 0;
    &::before {
      width: 100%;
      height: 100%;
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      background: transparent url('../images/btn-detail.png') no-repeat 0 0;
      background-size: 49.1rem auto;
    }
    &::after {
      width: 100%;
      height: 100%;
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      background: transparent url('../images/btn-detail.png') no-repeat 0 -10.8rem;
      background-size: 49.1rem auto;
      opacity: 0;
      transition: opacity 300ms ease-in-out;
    }
    &:hover {
      &::after {
        opacity: 1;
      }
    }
  }
}